.allPage {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.titleBar {
  background-color: #065f27; /* Blue color */
  text-align: center;
  width: 100vw;
}

.title {
  margin: 0;
  color: #fff;
}

.underPage {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100%;
}

.sidebar {
  width: 20%;
  background-color: #f4f4f4;
  padding: 20px;
  border-radius: 10px;
}

.sidebar ul {
  justify-content: center;
}

.sidebar ul li {
  cursor: pointer;
  margin-bottom: 10px;
}

.content {
  width: 80%;
  padding: 20px;
}
  
label {
  display: block;
  margin-bottom: 5px;
}

.survey-form-container {
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

.question-container {
  background-color: #4fab65; /* Light green background */
  margin-left: auto; /* Center the box horizontally */
  margin-right: auto; /* Center the box horizontally */
  width: 30vw;
  border-radius: 10px; /* Rounded corners */
  padding: 10px;
  margin-bottom: 10px; /* Add some spacing between questions */
}

.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 10px;
}